@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@500&display=swap);
.display {
  background-color: var(--display-color);
  color: white;
  font-size: 2rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 0.75em;
}

.util,
.digit,
.operator {
  justify-self: center;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.util,
.digit {
  background-color: var(--digit-color);
}

.util:hover,
.digit:hover {
  background-color: var(--digit-color-hover);
}

.operator {
  background-color: var(--operator-color);
}

.operator:hover {
  background-color: var(--operator-color-hover);
}

.grid {
  display: grid;
  grid:
    "display display display display" 10vh
    "util-0 util-1 util-2 operator-0" 10vh
    "digit-0 digit-1 digit-2 operator-1" 10vh
    "digit-3 digit-4 digit-5 operator-2" 10vh
    "digit-6 digit-7 digit-8 operator-3" 10vh
    "digit-9 digit-9 digit-10 operator-4" 10vh
    / 1fr 1fr 1fr 1fr;
}

:root {
  --display-color: #858693;
  --operator-color: #f5913e;
  --operator-color-hover: #f5903ec2;
  --digit-color: #e0e0e0;
  --digit-color-hover: #e0e0e088;
}

#root {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.app-container {
  min-width: 400px;
  width: 50vw;
  height: 50vh;
  justify-self: center;
  align-self: center;
}

body {
  margin: 0;
  font-family:
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    'Roboto',
    'Oxygen',
    'Ubuntu',
    'Cantarell',
    'Fira Sans',
    'Droid Sans',
    'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.nav-title {
  font-family: 'Caveat', cursive;
  font-size: 3em;
  text-align: center;
}

.nav-menu-list {
  display: flex;
  list-style: none;
}

.nav-menu-list li {
  padding: 1em;
}

.nav-menu-list a {
  text-decoration: none;
  color: black;
}

.nav-menu-list a:hover {
  text-decoration: underline;
}

.active-link {
  font-weight: bold;
}

